import logoDiscord from "./discord.svg";
import logoGithub from "./github.svg";
import scrollToHome from "../../utils/scrollToTop";
import "./Footer.css";

function Footer() {
  return (
    <div className="bgBase footer">
      <div className="footerGrid">
        <div>
          <h3>Sobre mi</h3>
          <p className="txt-left">
            Cuento con 5 años experiencia desarrollando aplicaciones web y
            moviles. Siempre he destacado por mi desempeño como FullStack para
            pequeños o grandes proyectos gracias a optimizar y crear sitios web
            asombrosos. Me encanta Dragon Ball y el Ajedrez, disfruto mucho
            jugando DBZFighter.
          </p>
        </div>
        <div>
          <h3>Objetivo</h3>
          <p className="txt-left">
            Quiero enfocarme en aprender machine learning e inteligencia
            artificial. Ya que considero que estas tecnologias son el futuro del
            internet como conocemos, y el siguiente paso en la revolucion de las
            maquinas.
          </p>
        </div>
        <div>
          <h3>Contactame</h3>
          <div className="icons_reference">
            <a
              rel="noreferrer"
              target="_blank"
              href="https://discord.gg/8aPjEr6P"
            >
              <img src={logoDiscord} alt="Error"></img>
            </a>
            <a
              rel="noreferrer"
              target="_blank"
              href="https://github.com/JavierMolines"
            >
              <img src={logoGithub} alt="Error"></img>
            </a>
          </div>
        </div>
      </div>
      <p id="contact" className="textoSubFooter bg-dark">
        © 2022 JavierMolines. Todos los derechos reservados.
      </p>
      <i onClick={scrollToHome} className="iconGoToTop fas fa-arrow-up"></i>
    </div>
  );
}

export default Footer;
