import { useState, useEffect } from "react";
import ALL_CURSES from "./data";
import "./Cursos.css";

function Cursos() {
  const [textButton, setTextButton] = useState("Mostrar todos");
  const [showFull, setShoFull] = useState(true);
  const [cursos, setCursos] = useState([]);
  const changeShoAll = () => {
    let configHaddler = {
      status: false,
      text: `Mostrar menos`,
      array: ALL_CURSES,
    };

    if (!showFull) {
      configHaddler.array = ALL_CURSES.slice(0, 4);
      configHaddler.text = `Mostros todos (${ALL_CURSES.length})`;
      configHaddler.status = true;
    }

    setCursos(configHaddler.array);
    setTextButton(configHaddler.text);
    setShoFull(configHaddler.status);
  };

  useEffect(() => {
    setTextButton(`Mostros todos (${ALL_CURSES.length})`);
    setCursos(ALL_CURSES.slice(0, 4));
  }, []);

  return (
    <div>
      <h1 id="certificados" className="titleProfolio">
        Mis Certificados
      </h1>

      <div id="idCursos" className="contenedorGrid gridSliceCursos">
        {cursos.map((data, count) => {
          return (
            <a
              key={count}
              className="containerProyectCursos"
              target="blank"
              href={data.url}
            >
              <img src={data.src} alt="ERROR"></img>
            </a>
          );
        })}
      </div>

      <div className="divButtonFlx">
        <button onClick={() => changeShoAll()} id="btnToggleCursos">
          {textButton}
        </button>
      </div>
    </div>
  );
}

export default Cursos;
